.App {
  text-align: center;
}
.width-auto {
  width: auto !important;
}
.App-logo {
  height: 40vmin;
  pointer-events: none;
}
.widthTranslate {
  -webkit-transition-property: width;
  -webkit-transition-duration: 0.5s;
  -webkit-transition-timing-function: ease-in-out;
  transition-property: width;
  transition-duration: 0.5s;
  transition-timing-function: ease-in-out;
}
.MuiInput-underline:after {
  border-bottom-color: rgba(75,192,192,0.4) !important;
}
@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.box-loader {
  height: 100vh;
  width: 100vw;
  display: flex;
  justify-content: center;
  align-items: center;
}
[class*="loader-"] {
  display: inline-block;
  width: 12em;
  height: 12em;
  color: inherit;
  vertical-align: middle;
  pointer-events: none;
}

.loader-07 {
  border: 0 solid transparent;
  border-radius: 50%;
  position: relative;
}
.loader-07:before, .loader-07:after {
  content: '';
  border: .9em solid rgba(0,0,0,0.5);
  border-radius: 50%;
  width: inherit;
  height: inherit;
  position: absolute;
  top: 0;
  left: 0;
  -webkit-animation: loader-07 1.3s linear infinite;
  animation: loader-07 1.3s linear infinite;
  opacity: 0;
}
.loader-07:before {
  -webkit-animation-delay: 0s;
  animation-delay: 0s;
}
.loader-07:after {
  -webkit-animation-delay: .7s;
  animation-delay: .7s;
}

@-webkit-keyframes loader-07 {
  0% {
    -webkit-transform: scale(0);
    transform: scale(0);
    opacity: 0;
  }
  50% {
    opacity: 1;
  }
  100% {
    -webkit-transform: scale(1);
    transform: scale(1);
    opacity: 0;
  }
}

@keyframes loader-07 {
  0% {
    -webkit-transform: scale(0);
    transform: scale(0);
    opacity: 0;
  }
  50% {
    opacity: 1;
  }
  100% {
    -webkit-transform: scale(1);
    transform: scale(1);
    opacity: 0;
  }
}
.min-chart-height {
  min-height: 666px;
}
.white-wrapper {
  padding: 20px;
  background-color: white;
  border-radius: 20px;
  box-shadow: 0 30px 50px 0 rgba(172, 172, 172, 0.3);
  -webkit-box-shadow: 0 30px 50px 0 rgba(163, 163, 163, 0.3);
}
@media (min-width: 1200px) {
  .white-wrapper {
    padding: 50px;
    border-radius: 50px;
  }
}
